.video {
  width: 280px;
  padding-left: 20px;
  padding-right: 20px;

  @media @tablet {
    width: 768px;
    margin: 0 auto;
  }

  @media @desktop {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
}

.video__clip {
  @media @tablet {
    position: relative;
    margin: 0 60px;
  }

  @media @desktop {
    position: relative;
    margin: 0 163px;
  }
}

.video__header {
  padding-top: 5px;
  padding-right: 2px;
  padding-bottom: 35px;
  padding-left: 2px;

  @media @tablet {
    padding: 65px 60px;
  }

  @media @desktop {
    padding-right: 42px;
    padding-bottom: 63px;
    padding-left: 42px;
    padding-top: 50px;
    text-align: center;
  }
}

.video__title {
  font-family: @font-family-base;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: @black;

  @media @tablet {
    font-size: 30px;
  }

  @media @desktop {
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 26px;
    text-align: center;
  }
}

.video__description {
  font-family: @font-family-base;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: @font-color-base;
  padding: 0 20px;

  @media @tablet {
    padding-left: 110px;
    padding-right: 110px;
  }

  @media @desktop {
    padding: 0 302px;
  }
}

.video__image {
  @media @tablet {
    width: 100%;
    height: 500px;
  }
}
