.benefits-header {
  background-color: #ffffff;
  position: relative;
  margin: 0 auto;
  padding: 26px 26px;
  padding-top: 40px;
  padding-bottom: 30px;
  text-align: center;

  @media @tablet {
    width: 490px;
    margin: 0 auto;
    text-align: center;
    padding: 50px 0;
  }
}

.benefits-header__title {
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}

.benefits-header__description {
  margin: 0;
  padding: 0;
  font-size: @font-size-base;
  line-height: 26px;
  text-transform: uppercase;
}
